import "core-js/modules/es.array.includes.js";
import "core-js/modules/es.function.name.js";
import { onUnmounted, reactive } from 'vue';
import Header from '@/components/Header.vue';
import Footer from '@/components/Footer.vue';
import { useRouter } from 'vue-router';
import { pathMap, localGet } from '@/utils';
import { fromCode } from '@/utils/validate';
export default {
  name: 'App',
  components: {
    Header: Header,
    Footer: Footer
  },
  setup: function setup() {
    var noMenu = ['/login'];
    var router = useRouter();
    var state = reactive({
      defaultOpen: ['1'],
      showMenu: true,
      currentPath: '/student_manage',
      count: {
        number: 1
      },
      userInfo: null
    }); // 监听浏览器原生回退事件

    if (window.history && window.history.pushState) {
      history.pushState(null, null, document.URL);
      window.addEventListener('popstate', function () {
        if (!localGet('token')) {
          state.showMenu = false;
        }
      }, false);
    }

    var unwatch = router.beforeEach(function (to, from, next) {
      if (to.path == '/login') {
        // 如果路径是 /login 则正常执行
        next();
      } else {
        // 如果不是 /login，判断是否有 token
        if (!localGet('token') && !sessionStorage.getItem("userInfo")) {
          // 如果没有，则跳至登录页面
          next({
            path: '/login'
          });
        } else {
          // 否则继续执行
          next();
        }
      }

      state.showMenu = !noMenu.includes(to.path);
      state.currentPath = to.path;
      document.title = pathMap[to.name];
    });
    onUnmounted(function () {
      unwatch();
    });
    return {
      state: state
    };
  },
  created: function created() {
    var user = sessionStorage.getItem("userInfo");

    if (user) {
      this.userInfo = JSON.parse(fromCode(user)); // 根据请求获取用户信息
    } else {
      this.$router.push("/login");
    }
  },
  data: function data() {
    return {
      isCollapse: true
    };
  },
  methods: {
    handleOpen: function handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose: function handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    closeAllMenu: function closeAllMenu() {
      this.isCollapse = !this.isCollapse;
    }
  }
};